/* MODAL */
.LoginModal {
    height: 80%;
    width: 70%;
    background: #FFFFFF;
    border: 3px solid #C4C4C4;
    box-sizing: border-box;
    box-shadow: 0px 4px 20px rgb(9 124 251 / 50%);
    border-radius: 20px;
}

/* DIV */
.TitleDiv {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 20%;
    width: 100%;
}

.InputDiv {
    display: flex;
    justify-content: center;
    align-items: center;

    flex-direction: column;

    height: 50%;
    width: 100%;
}

.ButtonDiv {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 30%;
    width: 100%;
}


/* TITLE */
.TitleText {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 50%;
    width: 100%;

    font-family: Work Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 40px;
    line-height: 47px;
    text-align: center;
    text-transform: uppercase;

    color: #007AFF;
}

/* INPUT */
.InputRow {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 40%;
    width: 100%;
}

.InputText {
    width: 90%;
    height:40%;

    background: #FFFFFF;
    border: 3px solid #80C44F;

    box-shadow: 0px 4px 20px #A9E166;
    border-radius: 15px;

    font-family: Work Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;

    color: #595959;
}

.InputText::placeholder {
    font-family: Work Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;

    color: #DAE8F1;
}

.InputText:focus, .InputText:focus{
    outline: none;
}


.LoginButton {
    height: 45%;
    width: 65%;

    margin: auto;
    border: none;
    
    font-family: Work Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 35px;

    color: #FFFFFF;

    background: linear-gradient(272.49deg, #80C44F 0.55%, #A9E166 65.3%);
    box-shadow: 0px 10px 15px rgba(169, 225, 102, 0.6);
    border-radius: 17.503px;

    cursor:pointer;
}