  .PlayerBox {
    height: 30%;
    width: 20%;

    margin-left: 2.5%;

    background-color: lightskyblue;

    display: flex;
    justify-content: center;
    align-items: center;

    flex-direction: row;

    border: 1px solid black;
    border-radius: 10px;
  }

  .NotConnectedBox {
    background-color: #f1f1f1;
    opacity: 0.3;
  }

  .MyPlayerBox {
    background: #FFFFFF;
    border: 3px solid #C4C4C4;
    box-sizing: border-box;
    box-shadow: 0px 4px 20px rgb(9 124 251 / 50%);
    border-radius: 20px;
  }

  .PlayerNameText {
    text-transform: uppercase;
    font-weight: bold;
  }