  .AuctionArea {
    height: 60%;
    width: 100%;

    background-color: #fafafa;

    display: flex;
    justify-content: center;
    align-items: center;

    flex-direction: column;
  }

  .AuctionBox {
    height: 25%;
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
  }

  .AuctionBoxDisabled {
    background-color: lightgray;
    opacity: 0.4;
  }

  .TimerText {
    height: 100%;
    width: 20%;
  
    display: flex;
    justify-content: center;
    align-items: center;
  
    
    font-weight: 700;
    font-size: 30px;
    line-height: 25px;
  
    color: var(--color-dark-1);
  }

  .HolderText {
    font-weight: 700;
    font-size: 30px;
    line-height: 25px;
  
    color: var(--color-dark-1);

    text-transform: uppercase;
  }


.RelaunchButton {
  height: 45%;
  width: 65%;

  margin: auto;
  border: none;
  
  font-family: Work Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 35px;

  color: #FFFFFF;

  background: linear-gradient(272.49deg, #80C44F 0.55%, #A9E166 65.3%);
  box-shadow: 0px 10px 15px rgba(169, 225, 102, 0.6);
  border-radius: 17.503px;
   
}

