:root {
    --color-primary: #6694FF;
    --color-primary-light0: #6694FF99;
    --color-primary-light1: #DAE8F1;
    --color-dark-3: #403F3F77;
    --color-dark-2: #403F3F;
    --color-dark-1: #82807D;
    --color-dark-0: #CCCAC6;
    --color-light-3: #e9e9e9;
    --color-light-2: #f1f1f1;
    --color-light-1: #fcfcfc;
    --color-light-0: #FFFFFF;
    --color-success: #80C44F;
    --color-warning: #c4c24f;
    --color-error: #c4664f;
}


html {
    height: 100%;
}

body {
    height: 100%;
    margin: 0;
    font-family: "Soleil", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif !important;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    user-select: none;
}

* {
    font-family: "Soleil", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif !important;
}

#root {
    height: 100%;
}

@font-face {
    font-family: "Soleil";
    src: url("./resources/fonts/soleil/SoleilRegular.woff2") format("woff2");
    font-display: auto;
    font-style: normal;
    font-weight: 400;
    font-stretch: normal;
}

@font-face {
    font-family: "Soleil";
    src: url("./resources/fonts/soleil/SoleilBold.woff2") format("woff2");
    font-display: auto;
    font-style: normal;
    font-weight: bold;
    font-stretch: normal;
}

@font-face {
    font-family: "Soleil";
    src: url("./resources/fonts/soleil/SoleilExtraBold.woff2") format("woff2");
    font-display: auto;
    font-style: normal;    
    font-weight: bolder;
    font-stretch: normal;
}

@font-face {
    font-family: "Soleil";
    src: url("./resources/fonts/soleil/SoleilBook.woff2") format("woff2");
    font-display: auto;
    font-style: normal;
    font-weight: 500;
    font-stretch: normal;
}

@font-face {
    font-family: "Soleil";
    src: url("./resources/fonts/soleil/SoleilLightItalic.woff2") format("woff2");
    font-display: auto;
    font-style: italic;
    font-weight: lighter;
    font-stretch: normal;
}

.react-toggle--checked .react-toggle-track{
    background-color: var(--color-primary) !important;
}

.react-toggle--checked .react-toggle-thumb{
    border-color: var(--color-primary) !important;
}

/*---------- Scrollbar --------------*/

/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: var(--color-primary-light1);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: var(--color-primary);
}